<script>
import { defineComponent } from 'vue'

export default defineComponent({
    name: 'ResultsManager',

    props: {
        selectedBill: {
            type: Object,
            default: () => null,
        },
        resultModalId: {
            type: String,
            default: 'result-modal',
        },
        sampleModalId: {
            type: String,
            default: 'sample-modal',
        },
    },

    data () {
        return {
            resultsForm: new window.Form({
                results: [],
                remarks: '',
                transaction_id: '',
            }),

            templates: [],

            sampleForm: new window.Form({
                id: '',
                transaction_id: '',
                collection_date: '',
                received_date: '',
                from: 0,
                source_from: '',
                specimen: '',
                equipment_code: '',
                status: 0
            }),

            sampleSources: [{
                name: 'Internal',
                id: 0
            }, {
                name: 'Referral',
                id: 1
            }],
        }
    },

    methods: {
        findTemplates() {

            this.resultsForm.results = []

            this.$httpClient.get('/patient-bills/' + this.selectedBill.id + '/templates')
                .then(({data}) => {

                    let templates = data.data

                    if (templates.length) {

                        this.resultsForm.results = templates.map((template) => {

                            let data = {
                                template_id: template.template_id,
                                template_type: template.template_type,
                            }

                            if (template.template_type === 0) {
                                data.result = template.results
                            }

                            if (template.template_type === 1) {
                                data.result = template.template_narrative;
                            }

                            return data;
                        })

                        let resultWithRemarks = templates.find((template) => template.result_remarks !== null);

                        if (resultWithRemarks) {
                            this.resultsForm.remarks = resultWithRemarks.result_remarks
                        }

                    }

                    this.templates = templates

                    this.sampleForm.transaction_id = this.templates[0].transaction_id
                    this.sampleForm.id = this.templates[0].sample_id
                    this.sampleForm.from = this.templates[0].sample_from
                    this.sampleForm.source_from = this.templates[0].sample_source_from
                    this.sampleForm.specimen = this.templates[0].sample_specimen
                    this.sampleForm.equipment_code = this.templates[0].sample_equipment_code
                    this.sampleForm.status = this.templates[0].sample_status

                }).catch((error) => {

                console.error(error.message)
            })
        },

        openResultsModal() {
            // this.selectedBill = bill

            this.resultsForm.transaction_id = this.selectedBill.id

            this.findTemplates()

            if (this.selectedBill.samples_collected === 0) {

                this.$bvModal.show('sample-modal')

                return
            }

            this.$bvModal.show('result-modal')
        },

        saveSamples() {
            this.sampleForm.post('/samples')
                .then(() => {

                    this.$toast.success('The samples have been collected')

                    this.$bvModal.hide('sample-modal');

                    setTimeout(() => {
                        window.location.reload()
                    }, 300)

                }).catch()
        },

        saveResults() {
            this.resultsForm.post('/results')
                .then(() => {

                    this.$bvModal.hide('result-modal');

                    this.$toast.success('The Results have been saved')

                    setTimeout(() => {
                        window.location.reload()
                    }, 300)

                    // this.retrievePatientBills()

                }).catch()
        },

        resetForm() {
            this.resultsForm.reset()
            this.sampleForm.reset()
        }
    }
})

</script>

<template>
    <div>
        <!-- Sample Modal-->
        <b-modal
            id="sample-modal"
            title="Sample Collection"
            no-close-on-backdrop
            footer-class="d-flex justify-content-end bg-muted-lt pt-2 border-top"
            content-class="bg-white rounded-3"
            modal-class="modal-blur"
            hide-header-close
            no-close-on-esc
            no-enforce-focus
            @hidden="resetForm">

            <div v-if="templates.length">

                <div class="mb-3">
                    <label for="" class="form-label">Sample Source</label>

                    <div>
                        <v-select v-model="sampleForm.from"
                                  label="name"
                                  :clearable="false"
                                  :reduce="option => option.id"
                                  :options="sampleSources">
                        </v-select>

                        <div class="text-danger"
                             v-if="sampleForm.hasError('from')"
                             v-text="sampleForm.getError('from')">
                        </div>

                    </div>
                </div>

                <div class="mb-3">
                    <label class="form-label">Request From</label>

                    <input type="text" class="form-control"
                           v-model="sampleForm.source_from"
                           placeholder="Doctor's Name, Hospital Name">
                    <div class="text-danger"
                         v-if="sampleForm.hasError('source_from')"
                         v-text="sampleForm.getError('source_from')">
                    </div>
                </div>

                <div class="row">
                    <div class="col-xl-6">
                        <div class="mb-3">
                            <label class="form-label">Specimen</label>

                            <input type="text" class="form-control"
                                   v-model="sampleForm.specimen">

                            <div class="text-danger"
                                 v-if="sampleForm.hasError('specimen')"
                                 v-text="sampleForm.getError('specimen')">
                            </div>
                        </div>

                    </div>

                    <div class="col-xl-6">
                        <div class="fv-row mb-10">
                            <label class="form-label">Equipment Code</label>

                            <input type="text" class="form-control"
                                   v-model="sampleForm.equipment_code">

                            <div class="text-danger"
                                 v-if="sampleForm.hasError('equipment_code')"
                                 v-text="sampleForm.getError('equipment_code')">
                            </div>
                        </div>

                    </div>
                </div>
            </div>


            <template #modal-footer="{cancel}">
                <button type="button" class="btn me-auto" @click.prevent="cancel" :disabled="sampleForm.processing">
                    Close
                </button>
                <button type="submit" class="btn btn-success" :disabled="sampleForm.processing"
                        @click="saveSamples">
                    Submit
                </button>
            </template>

        </b-modal>


        <!-- Results Modal-->
        <b-modal
            id="result-modal"
            title="Results Management"
            content-class="bg-white rounded-3 80vh"
            modal-class="modal-blur"
            size="xl"
            hide-footer
            hide-header-close
            no-close-on-esc
            scrollable
            no-enforce-focus
            @hidden="resetForm">

            <div v-if="templates.length">
                <table class="styled-table table-sm table-vcenter">
                    <thead>
                    <tr>
                        <th class="ps-2" style="line-height: 1; ">TEST NAME</th>
                        <th class="ps-2" style="line-height: 1; ">UNITS</th>
                        <th class="ps-2" style="line-height: 1; ">REF. RANGE</th>
                        <th class="ps-2" style="line-height: 1; ">RESULTS</th>
                    </tr>
                    </thead>

                    <tbody v-for="(template, index) in templates" :key="index">

                    <tr v-if="template.template_type === 0" class="">

                        <td class="ps-2" width="35%">

                            <span style="display: block; margin-bottom: 2px; font-weight: bold">{{
                                    template.name
                                }}</span>

                            <span
                                style="display: inline-block; white-space: break-spaces; margin-top: 5px; color: blueviolet"
                                v-if="template.remarks"
                                v-html="template.remarks">

                            </span>

                        </td>

                        <td width="25%">
                            {{ template.units }}
                        </td>

                        <td width="20%">
                            <span v-if="template.template_type === 0 && template.reference_type === 0">
                                {{ template.reference_range }}
                            </span>
                        </td>

                        <td v-if="template.template_type === 0 && template.reference_type === 0" width="20%"
                            class="ps-2">
                            <input type="text" class="form-control form-control-sm"
                                   v-model="resultsForm.results[index].result"
                                   style="padding: 0.2375rem 0.75rem">
                        </td>

                        <td v-if="template.template_type === 0 && template.reference_type === 1" width="20%">

                            <input type="text"
                                   :list="template.template_id"
                                   class="form-control form-control-sm"
                                   v-model="resultsForm.results[index].result">

                            <datalist :id="template.template_id">
                                <option v-for="value in template.non_range_flag"
                                        :value="value" v-text="value">
                                </option>
                            </datalist>

                        </td>

                    </tr>

                    <tr v-if="template.template_type === 1">

                        <td width="35%">

                            <span style="display: block; margin-bottom: 2px">{{ template.name }}</span>

                            <span
                                style="display: inline-block; white-space: break-spaces; margin-top: 5px; color: blueviolet"
                                v-if="template.remarks"
                                v-html="template.remarks">

                            </span>

                        </td>

                        <td width="25%%">
                            {{ template.template_narrative }}
                        </td>

                        <td width="20%"></td>

                        <td width="20%" class="w-25" v-if="template.template_type === 1">
                            <!--                                <input type="text" class="form-control form-control-sm form-control-solid"-->
                            <!--                                       v-model="resultsForm.results[index].narrative">-->

                            <!--                            <div class="fv-plugins-message-container invalid-feedback"-->
                            <!--                                 v-if="form.hasError('resultsForm.results[index].result')"-->
                            <!--                                 v-text="form.getError('resultsForm.results[index].result')">-->
                            <!--                            </div>-->
                        </td>


                    </tr>

                    </tbody>
                </table>

                <div class="mt-3">
                    <textarea name="" id="" cols="30" rows="3"
                              class="form-control form-control-solid"
                              placeholder="Remarks"
                              v-model="resultsForm.remarks">
                    </textarea>
                </div>

                <div class="fv-row" v-if="templates[0].template_type === 1">
                    <label for="" class="form-label">Please Enter the Results</label>
                    <textarea cols="30" rows="15"
                              class="form-control form-control-solid"
                              v-model="resultsForm.results[0].result">
                    </textarea>
                </div>

                <div class="row mt-3">
                    <div class="col-4 mx-auto">
                        <div class="d-flex justify-content-center">
                            <button type="submit"
                                    class="btn btn-light me-3 d-inline-block"
                                    :disabled="resultsForm.processing"
                                    @click="$bvModal.hide('result-modal')">
                                Close
                            </button>

                            <button type="submit"
                                    class="btn btn-primary d-block"
                                    @click="saveResults" :disabled="resultsForm.processing">
                                Submit
                            </button>
                        </div>

                    </div>


                </div>
            </div>

        </b-modal>

    </div>
</template>

